<template>
    <div class="mui-nba">
        <Header></Header>
        <!--NBA-->
        <div class="mui-card" v-for="item of list">
            <div class="mui-card-content-inner">
                <span>{{item.title}}</span>
            </div>
            <div class="mui-shorl">
                <router-link :to="`/particulars/:${item.lid}`"><img :src="item.jmg" /></router-link>
            </div>
                <span>{{item.uname}}&nbsp;&nbsp;{{item.ment}}&nbsp;&nbsp;{{item.ctime | datetimeFilter}}</span>
        </div>
        <mt-button size="large" @click="getMet"></mt-button>
    </div>
</template>
<script>
import {Toast} from  "mint-ui";
import Header from '@/components/header/search.vue'
export default {
    components: {
    Header
  },
    data(){
      return {
        msg:"",
        pageIndex:0, //
        pageSize:2,  //
        pageCount:1,  //
        list:[]
      }
  },
  created(){
    this.getMet();
  },
  methods: {
        getMet(){
            this.pageIndex++;
            var pno=this.pageIndex;
            var ps=this.pageSize;
            //3.url
            //4.ajax
            this.axios.get("nba",{params:{
                pno,pageSize:ps
            }}).then(result=>{
                //console.log(result);
                //5. 
                var rows=this.list.concat(result.data.data);
                this.list=rows;
                if(result.data.data==''){
                  Toast('~');
                }
                //console.log(this.list)
            })
        }
  }
}
</script>
<style>
.mui-card-content-inner{
    color:#333 !important;
    font-size:0.9rem;
}
</style>


